import TabBar from "./TabBar";
import { listData } from "../../server/employee";

import MescrollVue from 'mescroll.js/mescroll.vue'
import "mescroll.js/mescroll.min.css";
export default {
  name: "employee",
  components: { TabBar,
  //  Mescroll 
  MescrollVue
   },
  data() {
    return {
       mescroll: null, // mescroll实例对象
       mescrollUp: { // 上拉加载的配置.
          callback: this.getList,
          htmlNodata: '<p class="upwarp-nodata">-- END --</p>',
          noMoreSize: 5, 
          page: {
            num: 0,
            size: 50,
            total: 0
          },
          empty: {
          warpId: "main", 
          icon: "./static/mescroll/mescroll-empty.png", 
          tip: "暂无相关数据~" //提示
          },
        htmlLoading:'<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">加载中..</p>',
      },
      dataList: [],
      dataContent:[],
      imgFlag:true,
	    flag:true,
      value: "",
      imgUrl: require("@/assets/img/Group.png"),
      enterUrl:require("@/assets/img/enter.png"),
      liveUrl:require("@/assets/img/live.png"),
      removeUrl:require("@/assets/img/remove.png"),
      imgCut: require("@/assets/img/cutOff.png"),
      imgF:require("@/assets/img/jiantou.png"),
      page:''
    };
  },
  created() {
   
  },
  mounted() {
  },
  methods: {
     mescrollInit (mescroll) {
      this.mescroll = mescroll  
    },
    handleSeach(){
       var page={num:1,size:50};
      this.getList(page)
    },
    getList(page, mescroll) {
    const that = this
      listData({
        page: page.num,
        limit: page.size,
        searchStr:this.value
      }).then(e => {
        // console.log(page.num);
        if (e.data.code == 200) {
          let arr = e.data.data.list
           let data = page.num == 1 ? [] : this.dataList;
           this.dataList = this.dataList.concat(arr)
           console.log(this.dataList);
           this.$nextTick(() => {
              this.mescroll.endSuccess(arr.length)
            })
        }
      });
    },
  //   upCallback(){
  //     var that = this;
  //     that.num = page.page
  //     that.getList(that.num);
  // },
    handleImg() {
      this.flag=!this.flag
      this.imgFlag=!this.imgFlag
    },
    handlePushEnter(){
      this.$router.push({ path: "/AddEmployee" });
    },
    handleLive(){
      this.$router.push({ path: "/LiveEmpoyee" });
    },
  }
};



